
                @use "sass:color";
                @use "sass:math";
                @import "./sass/0-settings/_colors.scss";
              
@import "../../sass/base";

.image {
  // Image widths.
  &__wrapper {
    position: relative;

    &.open {
      .image__caption {
        height: fit-content;
        width: fit-content;
        max-width: 80%;
        max-height: 150px;
        padding: $wa-spacing-03;

        @include desktop {
          max-width: calc((100% / 3) * 2);
        }
      }

      .image__button--close {
        display: block;
      }

      .image__button--open {
        display: none;
      }
    }
  }

  &--full {
    .image__wrapper {
      @include grid-item-span(4, 6, 12);

      height: auto;
    }
  }

  &--large {
    @include container-md();

    .image__wrapper {
      @include grid-item-span(4, 6, 12);
    }
  }

  &--small {
    @include container-md();
    @include grid();

    .image__wrapper {
      @include grid-item-span(4, 3, 6);
    }
  }

  img {
    display: block;
    width: 100%;
    height: auto;
  }

  &__content {
    margin: 0;
  }

  &__caption {
    background-color: $wa-cream;
    font-size: 1rem;
    padding: $wa-spacing-03;
    line-height: 1.5;
    margin-top: -$wa-spacing-01;
    border-radius: 0 0 calculate-rem(2) calculate-rem(2);
  }

  &__credit {
    font-size: calculate-rem(14);
    line-height: 1.5;
    color: $wa-navy-80;
    display: block;
  }

  &--expandable {
    .image__caption {
      position: absolute;
      bottom: 16px;
      right: 12px;
      max-width: 80%;
      border-radius: $radius-xs;
      height: 0;
      width: 0;
      padding: 0;
      max-height: 0;
      overflow-y: hidden;
      transition-property: all;
      transition-duration: 0.5s;
      transition-timing-function: cubic-bezier(1, 1, 1, 1);
    }
  }

  &--below-image {
    margin-bottom: $wa-spacing-04;

    .image__caption {
      position: relative;

      &::after {
        content: "";
        display: block;
        position: absolute;
        background-image: url("/themes/custom/wateraid/images/icons/wa-curve-low-cream.svg");
        background-position: center;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        width: 100%;
        left: 0;
        top: -1px;
        height: 100%;
        z-index: -1;
      }
    }
  }

  &__button {
    position: absolute;
    bottom: 12px;
    right: 8px;
    z-index: 10;

    &--close {
      display: none;
    }
  }
}

